@import "./mixins";
#tile-section {
  padding-top: 60px;
  padding-bottom: 60px;
  .row {
    margin-right: 0;
    @include my_bp(sm) {
      margin-right: -15px;
    }
  }
  .grid {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    justify-content: start;
    line-height: 30px;
    width: 100%;
    flex-wrap: wrap;
    @include my_bp(sm) {
      margin: 5px;
    }
    .item {
      color: white;
      font-weight: bold;
      font-size: 1.5em;
      text-align: center;
      flex: 1 1 auto;
      height: auto;
      width: 45%;
      margin: 5px;
      transition: all .5s ease-in-out;
      text-decoration: none;
      filter: grayscale(100%);
      cursor: pointer;
      color: rgba(0, 0, 0, .4);
      @include my_bp(sm) {
        width: 25%;
        margin: 0;
      }
      &:before {
        content: '';
        float: left;
        padding-top: 100%;
      }
      &:hover {
        filter: grayscale(0%);
        color: rgba(0, 0, 0, .8);
      }
      &.double {
        display: flex;
        flex-direction: row;
        flex: 2 1 auto;
        width: 100%;
        // margin: 9px;
        @include my_bp(sm) {
          width: 50%;
        }
        &:before {
          content: '';
          float: left;
          padding-top: 50%;
        }
        .content {
          width: 50%;
        }
      }
      &.half {
        flex: 0.5;
        @include my_bp(sm) {
          flex: 1 1 auto;
        }
      }
      &.flip {
        perspective: 1000px;
        &:focus {
          .flip-container {
            transform: rotateY(180deg);
          }
        }
        .flip-container {
          height: 100%;
          position: relative;
          transition: transform 0.8s;
          transform-style: preserve-3d;
          //transition-delay: 1s;
          .front,
          .back {
            border: 1px solid rgba(200, 200, 200, .4);
            margin: 5px;
            padding: 10px;
            display: flex;
            flex: 1 1 auto;
            height: 100%;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            backface-visibility: hidden;
            position: absolute;
          }
          .back {
            transform: rotateY(180deg);
          }
          .front {
            h4 {
              font-weight: 700;
              text-transform: capitalize;
              color: #3fafef;
            }
          }
          p {
            text-align: justify;
            font-size: .9rem;
            line-height: 1.2rem;
          }
        }
      }
      .content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100%;
        border: 1px solid rgba(200, 200, 200, .4);
        margin: 5px;
        padding: 10px;
        flex: 1 1 auto;
        width: 100%;
        @include my_bp(sm) {
          width: auto;
        }
        p {
          text-align: justify;
          font-size: .9rem;
          line-height: 1.2rem;
        }
        &.launch {
          align-items: flex-start;
          h4 {
            font-weight: normal;
            color: #3fafef;
          }
          h2 {
            font-size: 2.5rem;
            margin: 0;
            color: #ed462f;
          }
          h1 {
            font-size: 6rem;
            color: #449667;
          }
        }
      }
    }
  }
}